import { useContext, useEffect } from "react";
import { API_KEY, API_URL } from "../config";
import { ShopContext } from "../context";
import { BacketList } from "./BacketList";
import { Cart } from "./Cart";
import { GoodsList } from "./GoodsList";
import { Preloader } from "./Preloader";

function Shop() {


  const {loading, order, setGoods,isBasketShow } = useContext(ShopContext);
 
  useEffect(function getGoods() {
    fetch(API_URL, {
      headers: {
        Authorization: API_KEY,
      },
    })
      .then((response) => response.json())
      .then((data) => {
        setGoods(transformationObj(data));
     
      })
      .catch((err) => {
        console.log(err);
      });
  }, []);

  const transformationObj = ({ shop }) => {
    return shop.map((item) => {
      return {
        id: item.mainId,
        name: item.granted[0].name,
        description: item.granted[0].description,
        price: item.price.finalPrice,
        img: item.granted[0].images.full_background,
      };
    });
  };

  return (
    <main className="content">
      <div className="container">
        <div className="row">
          <Cart quantity={order.length} />
          {loading ? <Preloader /> : <GoodsList /> }
          {isBasketShow && <BacketList />}
        </div>
      </div>
    </main>
  );
}

export { Shop };
