import { createContext, useReducer } from "react";
import { reducer } from './reducer'

export const ShopContext = createContext();

const initialState = {
    goods: [],
    loading: true,
    order: [],
    isBasketShow: false
}


export const ContextProvider = ({children}) => {
    

    const [value, dispatsh] = useReducer(reducer, initialState);


    value.removeFromBasket = (itemId) => {
        dispatsh({type: 'REMOVE_FROM_BASKET', payload: {id: itemId}})
    }

    value.addToBasket = (item) => {
        dispatsh({type: 'ADD_TO_BASKET', payload: item})
    }

    value.incQuantity = (itemId) => {
        dispatsh({type: 'INC_QUANTITY', payload: {id: itemId}})
    }

    value.decQuantity = (itemId) => {
        dispatsh({type: 'DEC_QUANTITY', payload: {id: itemId}})
    }

    value.handleBacketShow = () => {
        dispatsh({type: 'BACKET_SHOW'})
    }

    value.setGoods = (data) => {
        dispatsh({type: 'SET_GOODS', payload: data})
    }

    return <ShopContext.Provider value={value}>
                {children}
           </ShopContext.Provider>
}