import { useContext } from "react";
import { ShopContext } from "../context";

function BacketItem(props) {
  const { incQuantity, decQuantity, removeFromBasket } = useContext(ShopContext);

  const { id, name, quantity, price } = props;

  console.log(props);

  return (
    <li key={id} className="collection-item">
      {name}{" "}
      <i
        className="material-icons basket-quantity"
        onClick={() => decQuantity(id)}
      >
        remove
      </i>{" "}
      x = {quantity}{" "}
      <i
        className="material-icons basket-quantity"
        onClick={() => incQuantity(id)}
      >
        add
      </i>{" "}
      = price: {price * quantity}
      <span className="secondary-content basket-delete">
        <i className="material-icons" onClick={() => removeFromBasket(id)}>
          close
        </i>
      </span>
    </li>
  );
}

export { BacketItem };
