export function reducer(state, { type, payload }) {
    switch (type) {
        case 'SET_GOODS':
            return {
                ...state,
                goods: payload || [],
                loading: false
            }
        case 'REMOVE_FROM_BASKET':
            console.log(payload.id)
            return {
                ...state,
                order: state.order.filter(el => el.id !== payload.id)
            }
        case 'ADD_TO_BASKET': {
            const itemIndex = state.order.findIndex(el => el.id === payload.id);
            let newOrder = null;
            if (itemIndex < 0) {
                const newItem = {
                    ...payload,
                    quantity: 1
                }
                newOrder = [...state.order, newItem]
            } else {
                newOrder = state.order.map((el, index) => {
                    if (index === itemIndex) {
                        return {
                            ...el,
                            quantity: el.quantity + 1
                        }
                    }
                    else {
                        return el
                    }
                })
            }
            return {
                ...state,
                order: newOrder
            }
        }
        case 'INC_QUANTITY': {
            const newOrder = state.order.map(el => {
                if(el.id === payload.id) {
                    const newQuantity = el.quantity+1;
                    return {
                        ...el,
                        quantity: newQuantity
                    }
                } else {
                    return el;
                }
            })
            return {
                ...state,
                order: newOrder
            }
        }
        case 'DEC_QUANTITY': {
            const newOrder = state.order.map(el => {
                if(el.id === payload.id) {
                    const newQuantity = el.quantity-1;
                    return {
                        ...el,
                        quantity: newQuantity > 0 ? newQuantity : 1
                    }
                } else {
                    return el;
                }
            })
            return {
                ...state,
                order: newOrder
            }
        }
        case 'BACKET_SHOW': {
            return {
                ...state,
                isBasketShow: !state.isBasketShow
            }
        }    
        default:
            return state;
    }
}